.contacts {
  margin: 0;
}

.contacts__wrapper {
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 620px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 940px;
    margin: 0 auto;
    position: relative;
  }
}

.contacts__intro {
  margin-top: 0;
  margin-bottom: 32px;
  padding-top: 43px;
  font-weight: 300;
  font-size: 24px;
  line-height: 28.8px;
  color: $blue;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 55px;
    margin-bottom: 33px;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
    padding-top: 60px;
    margin-bottom: 40px;
  }
}

.contacts__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-around;
  }
}

.contacts__item {
  text-align: center;
  min-height: 75px;

  @media (min-width: $tablet-width) and (max-width: 959px) {
    width: 241px;
    margin-bottom: 28px;
  }
}

.contacts__item:nth-child(1) {
  @media (min-width: $tablet-width) {
    margin-right: 22px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 6px;
    margin-right: 29px;
  }
}


@media (max-width: 659px) {
  .contacts__item:last-child {
    margin-top: 5px;
  }
}

@media (min-width: $desktop-width) {
  .contacts__item:first-of-type {
    order: 1;
  }

  .contacts__item:nth-child(2) {
    order: 3;
    position: relative;
    left: 11px;
  }

  .contacts__item:last-of-type {
    order: 2;
  }
}

.contacts__description {
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  color: $blue;
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 3px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 21px;
  }
}

.contacts__info {
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  color: $pink;
  margin: 0;
}

.contacts__image {
  position: absolute;
  right: 400px;
  width: 1200px;
  height: 123px;
}
