.features {
  display: block;
}

.features__wrapper {
  @media (min-width: $tablet-width) {
    width: 620px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 940px;
    margin: 0 auto;
  }
}

.features__intro {
  padding-top: 40px;
  margin-right: auto;
  margin-left: auto;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 620px;
    padding-top: 47px;
    padding-bottom: 26px;
  }

  @media (min-width: $desktop-width) {
    width: 940px;
    padding-top: 62px;
    padding-bottom: 57px;
  }
}

.features__title {
  color: $blue;
  font-weight: 300;
  font-size: 24px;
  line-height: 28.8px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 55px;
    width: 620px;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
    width: 940px;
  }
}
